import React from 'react';
import Button from '@material-ui/core/Button';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { makeStyles } from '@material-ui/core/styles';

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
      marginBottom: theme.spacing(2),
    },
  },
}));

export default function SnackBar() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  return (
    <div className={classes.root}>
    
      <Button style={{width: "100%"}} variant="outlined" onClick={handleClick}>
        Validate Cedentials
      </Button>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success">
          You are Validated to begin using Stalk-Tok!
        </Alert>
      </Snackbar>
      <Alert severity="info">Freely Stalk and Be Stalked by Strangers</Alert>
      <Alert severity="success">Sign up and get Stalked Now</Alert>
      <Alert severity="warning">Your Data will be sold to the highest bidder for Stalk-Talk profit!</Alert>
      <Alert severity="error">By using Stalk-Tok you agree to Stalk-Tok's slimy terms of service.</Alert>
    </div>
  );
}
