import React, { useEffect } from 'react';
import Usercard from './Usercard'
// material ui
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { useContext } from 'react';
import { SearchContext } from '../containers/SearchContext';



const useStyles = makeStyles((theme) => ({
  grid: {
    flexGrow: 1
  }
}));

function Cardlist(props){

        // gain access to SearchContext
    const searchNow = useContext(SearchContext)

      // Similar to componentDidMount and componentDidUpdate:
    useEffect(() => {
      // Update the document title using the browser API
      // console.log('useeffect list');
    });

    // console.log('CardListprops', props.JSONdataArray);
    const {JSONdataArray} = props;
    const classes = useStyles();

    const filteredSearch = JSONdataArray.filter(user => {
      return user.name.first.toLowerCase().includes(searchNow.search.toLowerCase());
    })
    if (filteredSearch.length === 0) {
      return(<>
        <h3>User can be found that fits your search of "{searchNow.search}". <br/> Try the Creep 50 more button</h3>
      </>) 
      
    } else {

    return(<>
       <Grid justify="center" container className={classes.grid}>
           { 
               filteredSearch.map((JSONdataArrayToCard, index)=>{
                return (<div key={JSONdataArrayToCard.login.uuid} >
                  <Grid  item xs>
                      <Usercard key={'usercard-' + JSONdataArrayToCard.login.uuid} {...JSONdataArrayToCard}/>
                  </Grid>
                  </div>)
               })
           }
       </Grid>
    </>) 

    }

};

export default Cardlist;



