import React, { useState, useEffect, useContext } from 'react';
import Cardlist from '../components/Cardlist'
import SnackBar from '../components/SnackBar'
import PrimarySearchAppBar from '../components/PrimarySearchAppBar'
// import { ReferenceDataContext, ReferenceDataContextProvider } from "../components/ReferenceDataContext"
import CssBaseline from '@material-ui/core/CssBaseline';
import Button from '@material-ui/core/Button';
// import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';
import './App.css';
import { SearchContext } from './SearchContext';
import stalktok from '../components/stoktoksfx.mp3'

const useStyles = makeStyles((theme) => ({
  button: {
    '& > *': {
      margin: theme.spacing(7),
    },
  },
}));

function App() {
  const classes = useStyles();

  const audioTune = new Audio(stalktok);

  // variable to play audio in loop
  const [playInLoop, setPlayInLoop] = useState(false);

  const getDataFromUrl = (count) => {
      const url = 'https://randomuser.me/api/?results=' + count
      // get the data from the api and convert into js object for use in this app
      fetch(url).then((response)=> response.json())
      // store the data in an array
      .then((data)=>{

          setApiData( data['results']);
          // console.log('got data');

      })
  }
  const [apiData, setApiData] = useState([]);
  const [count, setCount] = useState(0);


  // load audio file on component load
  useEffect(() => {
    audioTune.load();

  }, [])


  // set the loop of audio tune
  useEffect(() => {
    audioTune.loop = playInLoop;
  }, [playInLoop])

  // Similar to componentDidMount and componentDidUpdate:
  useEffect(() => {
    // Update the document title using the browser API
    document.title = `You are creeping ${count} people`;

  });

  const handleCreepClick = () => {
    getDataFromUrl(50);
    setCount(prevCount => prevCount + 1);

    audioTune.play();

  }


  // gain access to SearchContext
  const searchNow = useContext(SearchContext)

  // console.log('apiData.length',apiData.length);
  if (apiData.length === 0) {
  console.log('nodata');
  return (
    <>
        <CssBaseline />
        {/* <Container maxWidth="lg"> */}
        <PrimarySearchAppBar></PrimarySearchAppBar>
        <div className="buffer"></div>

        <h1>Welcome to Stalk-Tok</h1>
        <SnackBar />

        <div className='center'>
        <Button className={classes.button} onClick={handleCreepClick} variant="contained">Insta-Creep <br></br> Click Now to Load 50 People  </Button> </div>
        <div>search = {searchNow.search}</div>


      {/* </Container> */}

    
    </>
  )
} else {
    return (
    <>
        <CssBaseline />
        {/* <Container maxWidth="lg"> */}
        <PrimarySearchAppBar></PrimarySearchAppBar>
        <div className="buffer"></div>
        <div className='center'>
        
        <Button className='center' onClick={handleCreepClick} variant="contained">Click here to creep 50 more people <br></br> You have creeped {count} times!!</Button> </div>
        <Cardlist key='cardlistkey1234' JSONdataArray={apiData}></Cardlist>


      {/* </Container> */}

    
    </>
  );
}



}


export default App;